import { db } from "@/lib/db";
import Header from "@/components/ui/containers/Header";
import BackButton from "@/components/ui/buttons/BackButton";
import PetCarousel from "@/components/pet/PetCarousel";

export default async function PetPage({ params }: { params: { id: string } }) {
  const [pet] = await db
    .selectFrom("pets")
    .where("pets.id", "=", params.id)
    .select(["name", "ageGroup", "breed", "size", "gender", "description"])
    .execute();

  const photos = await db
    .selectFrom("petImages")
    .where("petId", "=", params.id)
    .orderBy("orderIdx")
    .select(["url"])
    .execute();

  if (!pet) return <p className="p-6">Pet not found.</p>;

  return (
    <>
      <Header
        left={<BackButton />}
        center={<h1 className="text-lg font-semibold">{pet.name}</h1>}
      />

      <PetCarousel photos={photos} alt={pet.name} />

      <div className="flex justify-between w-full mx-auto max-w-md md:max-w-xl gap-4 py-4 m">
        <div className="flex flex-1/3 flex-col text-center bg-red-100 rounded-md justify-center items-center h-18 my-auto">
          <span className="text-gray-500">Gender</span>
          <span className="font-bold">{pet.gender}</span>
        </div>
        <div className="flex flex-1/3 flex-col text-center bg-blue-100 rounded-md justify-center items-center h-18">
          <span className="text-gray-500">Age</span>
          <span className="font-bold">{pet.ageGroup}</span>
        </div>
        <div className="flex flex-1/3 flex-col text-center bg-green-100 rounded-md  justify-center h-18 items-center">
          <span className="text-gray-500">Size</span>
          <span className="font-bold">{pet.size}</span>
        </div>
      </div>

      {pet.description && <p>{pet.description}</p>}
    </>
  );
}
